/**
 *   JSONP Google Sheets Service  COMSPEC
 *   -   
 *      
 *     
 */

import config from '../config/environment';

class JSONPGoogleSheetsService {
  constructor() {
    this.scriptUrl = config.GOOGLE_SCRIPT_URL;
    this.callbackCounter = 0;
    
    console.log('[JSONPGoogleSheetsService]    URL:', this.scriptUrl);
    console.log('[JSONPGoogleSheetsService]  :', config.ENVIRONMENT);
    console.log('[JSONPGoogleSheetsService]  CORS    JSONP!');
    
    if (!this.scriptUrl) {
      console.error('[JSONPGoogleSheetsService]  Google Apps Script URL  ');
      throw new Error('Google Apps Script URL  ');
    }
    
    //    window   
    if (typeof window !== 'undefined') {
      window.jsonpGoogleSheetsService = this;
    }
  }

  /**
   *    callback 
   */
  generateCallbackName() {
    this.callbackCounter++;
    return `jsonpCallback${Date.now()}_${this.callbackCounter}`;
  }

  /**
   *    Google Apps Script
   */
  testConnection() {
    return new Promise((resolve, reject) => {
      const callbackName = this.generateCallbackName();
      
      //   callback 
      window[callbackName] = (response) => {
        console.log('  :', response);
        delete window[callbackName]; //  callback
        resolve(response);
      };
      
      //  JSONP 
      const script = document.createElement('script');
      const params = new URLSearchParams({
        action: 'test',
        callback: callbackName
      });
      
      script.src = `${this.scriptUrl}?${params.toString()}`;
      script.onerror = () => {
        delete window[callbackName];
        reject(new Error('   Google Apps Script'));
      };
      
      document.head.appendChild(script);
      
      //     20 
      setTimeout(() => {
        if (document.head.contains(script)) {
          document.head.removeChild(script);
        }
        if (window[callbackName]) {
          delete window[callbackName];
          reject(new Error(' '));
        }
      }, 20000); //  15000,  20000
    });
  }

  /**
   *       JSONP
   */
  saveOrder(orderData, isConsultationMode = false, source = 'Website') {
    return new Promise((resolve, reject) => {
      const callbackName = this.generateCallbackName();
      
      //   callback 
      window[callbackName] = (response) => {
        if (config.DEBUG_MODE) {
          console.log('   Google Apps Script:', response);
        }
        
        //   CLEANUP  
        setTimeout(() => {
          delete window[callbackName];
        }, 500); //   
        
        if (response.success) {
          resolve(response);
        } else {
          reject(new Error(response.error || ' '));
        }
      };
      
      //  
      const params = {
        action: 'saveOrder',
        name: orderData.name || '',
        phone: orderData.phone || '',
        email: orderData.email || '',
        product: orderData.product || '',
        deliveryType: orderData.deliveryType || '',
        address: orderData.address || orderData.deliveryAddress || '',
        region: orderData.region || '',
        message: orderData.message || '',
        source: `${source}-${config.ENVIRONMENT}`,
        mode: isConsultationMode ? 'consultation' : 'order',
        Loading_Point: orderData.loadingPoint || orderData.Loading_Point || '',
        callback: callbackName
      };
      
      //    (  debug )
      if (config.DEBUG_MODE) {
        console.log('   :', params);
        console.log(' Loading_Point :', params.Loading_Point);
      }
      
      //  URL  
      const urlParams = new URLSearchParams();
      Object.keys(params).forEach(key => {
        if (params[key] !== undefined && params[key] !== null && params[key] !== '') {
          urlParams.append(key, params[key]);
        }
      });
      
      const finalUrl = `${this.scriptUrl}?${urlParams.toString()}`;
      
      if (config.DEBUG_MODE) {
        console.log('  URL:', finalUrl);
      }
      
      //    JSONP 
      const script = document.createElement('script');
      script.src = finalUrl;
      script.onerror = () => {
        setTimeout(() => {
          delete window[callbackName];
        }, 500);
        reject(new Error('  '));
      };
      
      document.head.appendChild(script);
      
      //     20  +  cleanup
      setTimeout(() => {
        if (document.head.contains(script)) {
          document.head.removeChild(script);
        }
        if (window[callbackName]) {
          delete window[callbackName];
          reject(new Error(' '));
        }
      }, 20000); //  10000,  20000
    });
  }

  /**
   *   Loading_Point
   */
  testLoadingPoint() {
    const testData = {
      name: '  JSONP',
      phone: '+380999888777',
      email: 'stable@test.com',
      product: '',
      deliveryType: 'delivery',
      address: ', . , 1',
      message: '   JSONP',
      Loading_Point: "  '"
    };
    
    console.log('    Loading_Point...');
    return this.saveOrder(testData, false, 'StableJSONPTest');
  }

  /**
   *       OrderModal
   */
  async submitOrder(orderData) {
    try {
      const cleanData = this.cleanOrderData(orderData);
      return await this.saveOrder(cleanData, false, 'ReactWebsite');
    } catch (error) {
      console.error('[JSONPGoogleSheetsService]   :', error);
      throw error;
    }
  }

  /**
   *     
   */
  cleanOrderData(orderData) {
    return {
      name: (orderData.name || '').trim(),
      phone: this.normalizePhone(orderData.phone || ''),
      email: (orderData.email || '').trim(),
      product: orderData.product || '',
      deliveryType: orderData.deliveryType || 'delivery',
      address: (orderData.address || '').trim(),
      loadingPoint: orderData.loadingPoint || '',
      message: (orderData.message || '').trim(),
      region: orderData.region || ''
    };
  }

  /**
   *   
   */
  normalizePhone(phone) {
    if (!phone) return '';
    
    const cleanPhone = phone.replace(/[^\d+]/g, '');
    
    if (cleanPhone.startsWith('+380')) {
      return cleanPhone;
    } else if (cleanPhone.startsWith('380')) {
      return '+' + cleanPhone;
    } else if (cleanPhone.startsWith('0')) {
      return '+38' + cleanPhone;
    } else if (cleanPhone.length === 9) {
      return '+380' + cleanPhone;
    }
    
    return cleanPhone;
  }
}

//   
const jsonpService = new JSONPGoogleSheetsService();

//  
if (typeof window !== 'undefined') {
  
  //  
  window.testStableJSONP = () => {
    console.log('    JSONP...');
    
    jsonpService.testConnection()
      .then(response => {
        console.log('  :', response);
        return jsonpService.testLoadingPoint();
      })
      .then(response => {
        console.log('  :', response);
        console.log('    !');
      })
      .catch(error => {
        console.error(' :', error);
      });
  };
  
  //   
  window.testConnection = () => {
    jsonpService.testConnection()
      .then(response => {
        console.log('   :', response);
      })
      .catch(error => {
        console.error('  :', error);
      });
  };

  //    submitOrder
  window.testSubmitOrder = () => {
    const testOrderData = {
      name: ' SubmitOrder',
      phone: '+380501234567',
      email: 'test@submitorder.com',
      product: '',
      deliveryType: 'delivery',
      address: ', . , 123',
      loadingPoint: "  '",
      message: '   submitOrder'
    };

    console.log('   submitOrder...');
    jsonpService.submitOrder(testOrderData)
      .then(response => {
        console.log(' submitOrder :', response);
      })
      .catch(error => {
        console.error('  submitOrder:', error);
      });
  };

  //       '
  window.COMSPEC_SERVICE_INFO = {
    environment: config.ENVIRONMENT,
    debugMode: config.DEBUG_MODE,
    scriptUrl: config.GOOGLE_SCRIPT_URL ? '' : '',
    version: 'Stable JSONP v2.0'
  };
}

export default jsonpService;