import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useOrderModal } from '../context/OrderModalContext';

const Products = () => {
  const [activeCategory, setActiveCategory] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');
  const location = useLocation();
  const { openOrderModal } = useOrderModal();

  //  URL    
  useEffect(() => {
    const urlParams = new URLSearchParams(location.search);
    const categoryParam = urlParams.get('category');
    
    if (categoryParam && ['gravel', 'sand', 'asphalt', 'concrete'].includes(categoryParam)) {
      setActiveCategory(categoryParam);
    } else {
      setActiveCategory('all');
    }
  }, [location.search]);

  const products = [
    {
      id: 1,
      title: '  5-20',
      category: 'gravel',
      price: '850 /',
      description: '      ',
      properties: ['', ' ', ' 5-20 '],
      image: '/images/gravel-5-20.jpg'
    },
    {
      id: 12,
      title: '  5-10',
      category: 'gravel',
      price: '350 /',
      description: '      ',
      properties: ['', ' ', ' 5-10 '],
      image: '/images/products/gravel-5-10.jpg'
    },
    {
      id: 2,
      title: '  20-40',
      category: 'gravel',
      price: '820 /',
      description: '    ',
      properties: ['', ' ', ' 20-40 '],
      image: '/images/gravel-20-40.jpg'
    },
    {
      id: 3,
      title: '  10-20',
      category: 'gravel',
      price: '750 /',
      description: '    ',
      properties: ['', ' ', ' 10-20 '],
      image: '/images/gravel-limestone.jpg'
    },
    {
      id: 4,
      title: '  ',
      category: 'sand',
      price: '420 /',
      description: '     ',
      properties: ['', '  ', ' 0-5 '],
      image: '/images/sand-river.jpg'
    },
    {
      id: 5,
      title: ' \'',
      category: 'sand',
      price: '380 /',
      description: '\'     ',
      properties: ['', '', ' 0-5 '],
      image: '/images/sand-quarry.jpg'
    },
    {
      id: 6,
      title: ' ',
      category: 'sand',
      price: '400 /',
      description: '  ',
      properties: ['', '', ' 0-3 '],
      image: '/images/sand-construction.jpg'
    },
    {
      id: 7,
      title: ' ',
      category: 'asphalt',
      price: '1200 /',
      description: '     ',
      properties: [' ', '', ' '],
      image: '/images/asphalt-hot.jpg'
    },
    {
      id: 8,
      title: ' ',
      category: 'asphalt',
      price: '1100 /',
      description: '     ',
      properties: [' ', '', ' '],
      image: '/images/asphalt-cold.jpg'
    },
    {
      id: 9,
      title: ' 200',
      category: 'concrete',
      price: '1800 /',
      description: '   200   ',
      properties: [' 200', 'B15', ' F100'],
      image: '/images/concrete-m200.jpg'
    },
    {
      id: 10,
      title: ' 300',
      category: 'concrete',
      price: '2000 /',
      description: '   300   ',
      properties: [' 300', 'B22.5', ' F150'],
      image: '/images/concrete-m300.jpg'
    },
    {
      id: 11,
      title: ' 400',
      category: 'concrete',
      price: '2200 /',
      description: '    ',
      properties: [' 400', 'B30', ' F200'],
      image: '/images/concrete-m400.jpg'
    }
  ];

  const categories = [
    { id: 'all', name: ' ' },
    { id: 'gravel', name: '' },
    { id: 'sand', name: '' },
    { id: 'asphalt', name: '' },
    { id: 'concrete', name: '' }
  ];

  const filteredProducts = products.filter(product => {
    const matchesCategory = activeCategory === 'all' || product.category === activeCategory;
    const matchesSearch = product.title.toLowerCase().includes(searchTerm.toLowerCase());
    return matchesCategory && matchesSearch;
  });

  //      
  const getActiveCategoryName = () => {
    const category = categories.find(cat => cat.id === activeCategory);
    return category ? category.name : ' ';
  };

  //      URL
  const handleCategoryChange = (categoryId) => {
    setActiveCategory(categoryId);
    
    //  URL   
    const newUrl = categoryId === 'all' 
      ? '/products' 
      : `/products?category=${categoryId}`;
    
    window.history.pushState(null, '', newUrl);
  };

  //   
  const handleOrderClick = (product) => {
    openOrderModal({
      product: product.category,
      preSelectedProduct: product.title,
      source: 'product-page'
    });
  };

  return (
    <div className="products-page">
      <section className="section">
        <div className="container">
          <h1 className="section-title"></h1>
          
          {/*    */}
          {activeCategory !== 'all' && (
            <div style={{
              textAlign: 'center',
              marginBottom: '1.5rem',
              padding: '0.75rem 1.5rem',
              backgroundColor: '#e6f3f3',
              borderRadius: '8px',
              color: '#008080',
              fontWeight: '600'
            }}>
               : {getActiveCategoryName()}
            </div>
          )}
          
          {/* Filters */}
          <div className="filters" style={{marginBottom: '2rem'}}>
            <div className="category-filters" style={{marginBottom: '1rem'}}>
              {categories.map(category => (
                <button
                  key={category.id}
                  className={`btn ${activeCategory === category.id ? 'btn-primary' : 'btn-outline'}`}
                  onClick={() => handleCategoryChange(category.id)}
                  style={{
                    marginRight: '0.5rem', 
                    marginBottom: '0.5rem',
                    backgroundColor: activeCategory === category.id ? '#008080' : 'transparent',
                    color: activeCategory === category.id ? '#ffffff' : '#008080',
                    borderColor: '#008080'
                  }}
                >
                  {category.name}
                </button>
              ))}
            </div>
            
            <div className="search-filter">
              <input
                type="text"
                placeholder=" ..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                style={{
                  padding: '0.75rem',
                  border: '2px solid #e9ecef',
                  borderRadius: '6px',
                  width: '300px',
                  maxWidth: '100%'
                }}
              />
            </div>
          </div>

          {/* Products Grid */}
          <div className="grid grid-3">
            {filteredProducts.map(product => (
              <div key={product.id} className="card">
                <div style={{
                  height: '200px',
                  backgroundColor: '#f8f9fa',
                  borderRadius: '8px',
                  marginBottom: '1rem',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: '#6c757d'
                }}>
                   
                </div>
                
                <h3>{product.title}</h3>
                <p className="price" style={{
                  fontSize: '1.25rem',
                  fontWeight: '600',
                  color: '#008080',
                  marginBottom: '1rem'
                }}>
                  {product.price}
                </p>
                
                <p>{product.description}</p>
                
                <div className="properties" style={{marginBottom: '1.5rem'}}>
                  {product.properties.map((prop, index) => (
                    <span
                      key={index}
                      style={{
                        display: 'inline-block',
                        background: '#e9ecef',
                        padding: '0.25rem 0.5rem',
                        borderRadius: '12px',
                        fontSize: '0.875rem',
                        marginRight: '0.5rem',
                        marginBottom: '0.5rem'
                      }}
                    >
                      {prop}
                    </span>
                  ))}
                </div>
                
                <div className="card-actions" style={{
                  display: 'flex',
                  gap: '0.5rem',
                  flexWrap: 'wrap'
                }}>
                  <button 
                    className="btn btn-accent"
                    onClick={() => handleOrderClick(product)}
                  >
                    
                  </button>
                  <Link 
                    to={`/certificates/${product.category}`} 
                    className="btn btn-primary"
                    style={{fontSize: '0.875rem'}}
                  >
                    
                  </Link>
                </div>
              </div>
            ))}
          </div>

          {filteredProducts.length === 0 && (
            <div style={{textAlign: 'center', padding: '3rem', color: '#6c757d'}}>
              <h3>  </h3>
              <p>     </p>
            </div>
          )}

          {/*     */}
          {activeCategory !== 'all' && (
            <div style={{textAlign: 'center', marginTop: '2rem'}}>
              <button 
                onClick={() => handleCategoryChange('all')}
                className="btn btn-primary"
                style={{padding: '0.75rem 2rem'}}
              >
                  
              </button>
            </div>
          )}
        </div>
      </section>
    </div>
  );
};

export default Products;