import React, { useState } from 'react';
import { Link } from 'react-router-dom';

const Articles = () => {
  const [activeCategory, setActiveCategory] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');

  const categories = [
    { id: 'all', name: ' ' },
    { id: 'products', name: ' ' },
    { id: 'construction', name: '  ' },
    { id: 'news', name: ' ' }
  ];

  const articles = [
    {
      id: 1,
      title: '     ',
      category: 'products',
      excerpt: '        .  ,     .',
      date: '2024-01-15',
      readTime: '5 ',
      image: '/images/articles/gravel-foundation.jpg',
      tags: ['', '', '']
    },
    {
      id: 2,
      title: '   ',
      category: 'products',
      excerpt: '   ,        .',
      date: '2024-01-10',
      readTime: '7 ',
      image: '/images/articles/asphalt-production.jpg',
      tags: ['', '', '']
    },
    {
      id: 3,
      title: '     ',
      category: 'construction',
      excerpt: '        . ,    .',
      date: '2024-01-05',
      readTime: '6 ',
      image: '/images/articles/winter-concrete.jpg',
      tags: ['', '', '']
    },
    {
      id: 4,
      title: 'COMSPEC   ISO 14001:2015',
      category: 'news',
      excerpt: '          .',
      date: '2023-12-20',
      readTime: '3 ',
      image: '/images/articles/iso-certificate.jpg',
      tags: ['', '', '']
    },
    {
      id: 5,
      title: '     ',
      category: 'construction',
      excerpt: '    \' ,        .',
      date: '2023-12-15',
      readTime: '4 ',
      image: '/images/articles/sand-calculation.jpg',
      tags: ['', '', '']
    },
    {
      id: 6,
      title: '     ',
      category: 'news',
      excerpt: '         120 /.    .',
      date: '2023-12-10',
      readTime: '3 ',
      image: '/images/articles/new-equipment.jpg',
      tags: ['', '', '']
    },
    {
      id: 7,
      title: '    ',
      category: 'products',
      excerpt: '   ,        .',
      date: '2023-12-05',
      readTime: '8 ',
      image: '/images/articles/sand-types.jpg',
      tags: ['', '', '']
    },
    {
      id: 8,
      title: '    ',
      category: 'construction',
      excerpt: '  ,       .',
      date: '2023-11-30',
      readTime: '6 ',
      image: '/images/articles/paving-stones.jpg',
      tags: ['', '', '']
    }
  ];

  const filteredArticles = articles.filter(article => {
    const matchesCategory = activeCategory === 'all' || article.category === activeCategory;
    const matchesSearch = article.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         article.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()));
    return matchesCategory && matchesSearch;
  });

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('uk-UA', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  return (
    <div className="articles-page">
      {/* Hero Section */}
      <section className="hero" style={{padding: '4rem 0'}}>
        <div className="container">
          <h1>  </h1>
          <p>    ,    </p>
        </div>
      </section>

      {/* Filters */}
      <section className="section">
        <div className="container">
          <div className="filters" style={{marginBottom: '3rem'}}>
            <div className="category-filters" style={{marginBottom: '1rem'}}>
              {categories.map(category => (
                <button
                  key={category.id}
                  className={`btn ${activeCategory === category.id ? 'btn-primary' : 'btn-outline'}`}
                  onClick={() => setActiveCategory(category.id)}
                  style={{marginRight: '0.5rem', marginBottom: '0.5rem'}}
                >
                  {category.name}
                </button>
              ))}
            </div>
            
            <div className="search-filter">
              <input
                type="text"
                placeholder=" ..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                style={{
                  padding: '0.75rem',
                  border: '2px solid #e9ecef',
                  borderRadius: '6px',
                  width: '300px',
                  maxWidth: '100%'
                }}
              />
            </div>
          </div>

          {/* Articles Grid */}
          <div className="grid grid-3">
            {filteredArticles.map(article => (
              <article key={article.id} className="card">
                <div style={{
                  height: '200px',
                  backgroundColor: '#f8f9fa',
                  borderRadius: '8px',
                  marginBottom: '1rem',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: '#6c757d'
                }}>
                   
                </div>
                
                <div className="article-meta" style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center',
                  marginBottom: '1rem',
                  fontSize: '0.875rem',
                  color: '#6c757d'
                }}>
                  <span>{formatDate(article.date)}</span>
                  <span>{article.readTime}</span>
                </div>
                
                <h3 style={{marginBottom: '1rem', lineHeight: '1.4'}}>
                  <Link 
                    to={`/articles/${article.id}`}
                    style={{textDecoration: 'none', color: 'inherit'}}
                  >
                    {article.title}
                  </Link>
                </h3>
                
                <p style={{marginBottom: '1rem', color: '#6c757d'}}>
                  {article.excerpt}
                </p>
                
                <div className="article-tags" style={{marginBottom: '1.5rem'}}>
                  {article.tags.map((tag, index) => (
                    <span
                      key={index}
                      style={{
                        display: 'inline-block',
                        background: '#e9ecef',
                        padding: '0.25rem 0.5rem',
                        borderRadius: '12px',
                        fontSize: '0.75rem',
                        marginRight: '0.5rem',
                        marginBottom: '0.25rem'
                      }}
                    >
                      #{tag}
                    </span>
                  ))}
                </div>
                
                <Link 
                  to={`/articles/${article.id}`}
                  className="btn btn-primary"
                  style={{width: '100%', textAlign: 'center'}}
                >
                   
                </Link>
              </article>
            ))}
          </div>

          {filteredArticles.length === 0 && (
            <div style={{textAlign: 'center', padding: '3rem', color: '#6c757d'}}>
              <h3>  </h3>
              <p>     </p>
            </div>
          )}
        </div>
      </section>

      {/* Newsletter Subscribe */}
      <section className="section dark">
        <div className="container">
          <div style={{textAlign: 'center', maxWidth: '600px', margin: '0 auto'}}>
            <h2>  </h2>
            <p style={{marginBottom: '2rem'}}>
                ,        
            </p>
            
            <form style={{
              display: 'flex',
              gap: '1rem',
              maxWidth: '400px',
              margin: '0 auto',
              flexWrap: 'wrap'
            }}>
              <input
                type="email"
                placeholder="  "
                style={{
                  flex: '1',
                  padding: '0.75rem',
                  border: '2px solid rgba(255,255,255,0.3)',
                  borderRadius: '6px',
                  background: 'rgba(255,255,255,0.1)',
                  color: 'white',
                  minWidth: '200px'
                }}
              />
              <button type="submit" className="btn btn-accent">
                
              </button>
            </form>
          </div>
        </div>
      </section>

      {/* Related Articles */}
      <section className="section gray">
        <div className="container">
          <h2 className="section-title"> </h2>
          <div className="grid grid-3">
            {articles.slice(0, 3).map(article => (
              <div key={article.id} className="card">
                <h4>{article.title}</h4>
                <p style={{color: '#6c757d', fontSize: '0.875rem'}}>
                  {formatDate(article.date)}  {article.readTime}
                </p>
                <Link to={`/articles/${article.id}`} className="btn btn-primary" style={{marginTop: '1rem'}}>
                  
                </Link>
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
};

export default Articles;