// src/pages/Home.js
import React from 'react';
import { Link } from 'react-router-dom';
import { useOrderModal } from '../context/OrderModalContext';

//    src/assets   
import gravelIcon from '../assets/icons/gravel.png';
import sandIcon from '../assets/icons/sand.png';
import asphaltIcon from '../assets/icons/asphalt.png';
import concreteIcon from '../assets/icons/concrete.png';

//     
import expertiseIcon from '../assets/icons/expertise.png';
import qualityIcon from '../assets/icons/quality.png';
import priceIcon from '../assets/icons/price.png';
import deliveryIcon from '../assets/icons/delivery.png';

const Home = () => {
  const { openOrderModal } = useOrderModal();

  const products = [
    {
      title: '',
      description: '     ',
      icon: gravelIcon,
      category: 'gravel'
    },
    {
      title: '',
      description: '  \'      ',
      icon: sandIcon,
      category: 'sand'
    },
    {
      title: '',
      description: '    ',
      icon: asphaltIcon,
      category: 'asphalt'
    },
    {
      title: '',
      description: '     ',
      icon: concreteIcon,
      category: 'concrete'
    }
  ];

  const services = [
    {
      title: '',
      description: '    '
    },
    {
      title: ' ',
      description: '   \'  '
    },
    {
      title: ' ',
      description: '    '
    }
  ];

  const benefits = [
    {
      title: '   ',
      description: '      ',
      icon: expertiseIcon
    },
    {
      title: ' ',
      description: '     ',
      icon: qualityIcon
    },
    {
      title: ' ',
      description: '    ',
      icon: priceIcon
    },
    {
      title: ' ',
      description: '      ',
      icon: deliveryIcon
    }
  ];

  return (
    <div className="home">
      {/* Hero Section */}
      <section className="hero">
        <div className="container">
          <h1>  !</h1>
          <p>       </p>
          <div className="hero-buttons">
            <button 
              className="btn btn-accent"
              onClick={() => openOrderModal({ 
                source: 'hero-section'
              })}
            >
              
            </button>
            <Link to="/products" className="btn btn-primary"></Link>
          </div>
        </div>
      </section>

      {/* Products Section */}
      <section className="section products-section">
        <div className="container">
          <h2 className="section-title"> </h2>
          <div className="grid grid-4">
            {products.map((product, index) => (
              <div key={index} className="card">
                {/*      */}
                <Link to={`/products?category=${product.category}`}>
                  <img 
                    src={product.icon} 
                    alt={product.title}
                    className="card-icon clickable-icon"
                  />
                </Link>
                
                {/*      */}
                <Link 
                  to={`/products?category=${product.category}`}
                  style={{ textDecoration: 'none', color: 'inherit' }}
                >
                  <h3 className="clickable-title" style={{ cursor: 'pointer' }}>
                    {product.title}
                  </h3>
                </Link>
                
                <p>{product.description}</p>
                <button 
                  className="btn btn-accent"
                  onClick={() => openOrderModal({ 
                    product: product.category,
                    source: 'product-card'
                  })}
                >
                  
                </button>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Services Section */}
      <section className="section dark">
        <div className="container">
          <h2 className="section-title"></h2>
          <div className="grid grid-3">
            {services.map((service, index) => (
              <div key={index} className="card">
                <h3>{service.title}</h3>
                <p>{service.description}</p>
              </div>
            ))}
          </div>
          <div style={{textAlign: 'center', marginTop: '2rem'}}>
            <Link to="/services" className="btn btn-primary"> </Link>
          </div>
        </div>
      </section>

      {/* Benefits Section */}
      <section className="section benefits-section">
        <div className="container">
          <h2 className="section-title"></h2>
          <div className="grid grid-4">
            {benefits.map((benefit, index) => (
              <div key={index} className="card">
                <img 
                  src={benefit.icon} 
                  alt={benefit.title}
                  className="card-icon"
                />
                <h3>{benefit.title}</h3>
                <p>{benefit.description}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Segments Section */}
      <section className="section gray">
        <div className="container">
          <h2 className="section-title"></h2>
          <div className="grid grid-2">
            <div className="card">
              <h3> </h3>
              <ul>
                <li> </li>
                <li> </li>
                <li> </li>
                <li>  </li>
              </ul>
              <button 
                className="btn btn-primary"
                onClick={() => openOrderModal({ 
                  source: 'companies-section'
                })}
              >
                 
              </button>
            </div>
            <div className="card">
              <h3>  </h3>
              <ul>
                <li> </li>
                <li> </li>
                <li> </li>
                <li> </li>
              </ul>
              <Link to="/retail-locations" className="btn btn-primary"> </Link>
            </div>
          </div>
        </div>
      </section>

      {/* Reviews Section */}
      <section className="section">
        <div className="container">
          <h2 className="section-title"> </h2>
          <div className="grid grid-3">
            <div className="card">
              <h3> ""</h3>
              <p>"  COMSPEC  3 .      ."</p>
            </div>
            <div className="card">
              <h3> </h3>
              <p>"  ,    . !"</p>
            </div>
            <div className="card">
              <h3> ""</h3>
              <p>"    .     ."</p>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Home;