import React from 'react';
import { Link } from 'react-router-dom';

const Footer = () => {
  return (
    <footer className="footer">
      <div className="container">
        <div className="footer-content">
          <div className="footer-section">
            <Link to="/" className="footer-logo-link">
              <img 
                src={process.env.PUBLIC_URL + '/images/logo.svg'}  
                alt="COMSPEC" 
                className="footer-logo"
              />
            </Link>
            <p>       </p>
          </div>
          
          <div className="footer-section">
            <h3></h3>
            <p>: <a href="tel:+380739272700">073 927 27 00</a></p>
            <p>Email: <a href="mailto:info@comspec.ua">comspec@comspec.ua</a></p>
            <p>: <a href="https://comspec.ua">comspec.ua</a></p>
          </div>
          
          <div className="footer-section">
            <h3></h3>
            <p><Link to="/products"></Link></p>
            <p><Link to="/services"></Link></p>
            <p><Link to="/about"> </Link></p>
            <p><Link to="/contacts"></Link></p>
            <p><Link to="/articles"></Link></p>
          </div>
          
          <div className="footer-section">
            <h3></h3>
            <p><a href="/privacy-policy">Privacy Policy</a></p>
            <p><Link to="/certificates/gravel"></Link></p>
            <p><a href="/price-list.pdf">-</a></p>
          </div>
        </div>
        
        <div className="footer-bottom">
          <p>&copy; 2025 COMSPEC.   .</p>
        </div>
      </div>
    </footer>
  );
};

export default Footer;