import React, { useState, useEffect } from 'react';

const Contacts = () => {
  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    email: '',
    message: ''
  });

  //     
  useEffect(() => {
    const hash = window.location.hash;
    if (hash) {
      //       ( )
      const hashParts = hash.split('#');
      if (hashParts.length > 2) {
        //     #,     
        const anchorId = '#' + hashParts.pop();
        const element = document.querySelector(anchorId);
        if (element) {
          setTimeout(() => {
            element.scrollIntoView({ 
              behavior: 'smooth', 
              block: 'start' 
            });
          }, 100);
        }
      }
      //      ( #/contacts)
    }
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Form submitted:', formData);
    //     
    alert('  !  \'    .');
    setFormData({ name: '', phone: '', email: '', message: '' });
  };

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  return (
    <div className="contacts-page">
      <section className="section">
        <div className="container">
          <h1 className="section-title"></h1>
          
          <div className="grid grid-2" style={{gap: '3rem'}}>
            {/* Contact Information */}
            <div>
              <div className="contact-section" style={{marginBottom: '2rem'}}>
                <h3> </h3>
                <div style={{marginBottom: '1rem'}}>
                  <strong>:</strong>
                  <p>. ,  , 17-</p>
                </div>
                <div style={{marginBottom: '1rem'}}>
                  <strong>:</strong>
                  <p>
                    <a href="tel:+380739272700" style={{color: '#008080', textDecoration: 'none'}}>
                      073 927 27 00
                    </a>
                  </p>
                  <p>
                    <a href="tel:+380445274700" style={{color: '#008080', textDecoration: 'none'}}>
                      044 527 47 00
                    </a>
                  </p>
                </div>
                <div>
                  <strong>Email:</strong>
                  <p>
                    <a href="mailto:sales.maxigran@gmail.com" style={{color: '#008080', textDecoration: 'none'}}>
                      sales@comspec.ua
                    </a>
                  </p>
                </div>
              </div>

              <div className="contact-section">
                <h3> </h3>
                <div style={{marginBottom: '1rem'}}>
                  <strong> :</strong>
                  <p>02090, . ,  , 17-, . 3</p>
                </div>
                <div style={{marginBottom: '1rem'}}>
                  <strong>:</strong>
                  <p>
                    <a href="tel:+380445274700" style={{color: '#008080', textDecoration: 'none'}}>
                      044 527 47 00
                    </a>
                  </p>
                </div>
                <div>
                  <strong>Email:</strong>
                  <p>
                    <a href="mailto:comspec@comspec.ua" style={{color: '#008080', textDecoration: 'none'}}>
                      comspec@comspec.ua
                    </a>
                  </p>
                </div>
              </div>

              <div className="messengers" style={{marginTop: '2rem'}}>
                <h4></h4>
                <div style={{display: 'flex', gap: '1rem', marginTop: '1rem'}}>
                  <a 
                    href="viber://chat?number=+380739272700" 
                    className="btn btn-primary"
                    style={{textDecoration: 'none'}}
                  >
                    Viber
                  </a>
                  <a 
                    href="https://t.me/comspec_ua" 
                    className="btn btn-primary"
                    style={{textDecoration: 'none'}}
                  >
                    Telegram
                  </a>
                </div>
              </div>
            </div>

            {/* Contact Form */}
            <div>
              <h3>  '</h3>
              <form onSubmit={handleSubmit}>
                <div className="form-group">
                  <label htmlFor="name">' *</label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    required
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="phone"> *</label>
                  <input
                    type="tel"
                    id="phone"
                    name="phone"
                    value={formData.phone}
                    onChange={handleChange}
                    placeholder="+380"
                    required
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="email">Email</label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="message"> *</label>
                  <textarea
                    id="message"
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    placeholder="  ..."
                    required
                  />
                </div>

                <button type="submit" className="btn btn-accent">
                  
                </button>
              </form>
            </div>
          </div>
        </div>
      </section>

      {/* Calculator Section */}
      <section id="calculator" className="section gray">
        <div className="container">
          <h2 className="section-title"> </h2>
          <div className="calculator" style={{
            background: 'white',
            padding: '2rem',
            borderRadius: '12px',
            maxWidth: '600px',
            margin: '0 auto'
          }}>
            <div className="grid grid-2">
              <div className="form-group">
                <label> </label>
                <select>
                  <option>  5-20</option>
                  <option>  20-40</option>
                  <option> </option>
                  <option> </option>
                  <option> 200</option>
                </select>
              </div>
              
              <div className="form-group">
                <label> (/)</label>
                <input type="number" placeholder="10" />
              </div>
              
              <div className="form-group">
                <label>  ()</label>
                <input type="number" placeholder="50" />
              </div>
              
              <div className="form-group">
                <label> </label>
                <select>
                  <option></option>
                  <option></option>
                </select>
              </div>
            </div>
            
            <div style={{textAlign: 'center', marginTop: '1.5rem'}}>
              <button className="btn btn-primary"> </button>
            </div>
            
            <div className="calculation-result" style={{
              marginTop: '1.5rem',
              padding: '1rem',
              background: '#f8f9fa',
              borderRadius: '8px',
              textAlign: 'center'
            }}>
              <h4> : 45 000 </h4>
              <p style={{color: '#6c757d', fontSize: '0.875rem'}}>
                *       
              </p>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Contacts;