import React from 'react';
import { useOrderModal } from '../context/OrderModalContext';
import useComspecSearch from '../hooks/useSearchNavigation'; //   

const Services = () => {
  const { openOrderModal } = useOrderModal();
  
  //  :    
  const { search, highlight, clearHighlights } = useComspecSearch();

  //       
  const handleConsultationClick = (serviceType) => {
    openOrderModal({
      source: 'services-page',
      serviceType: serviceType //     
    });
  };

  const deliveryServices = [
    {
      title: ' ',
      description: '        ',
      features: [
        ' \' ( 50 )',
        ' ',
        '  - ',
        ' '
      ],
      image: '/images/railway-delivery.jpg'
    },
    {
      title: ' ',
      description: '     ',
      features: [
        ' ',
        ' ',
        ' ',
        '  \''
      ],
      image: '/images/truck-delivery.jpg'
    }
  ];

  const equipmentServices = [
    {
      title: '',
      description: '    ',
      equipment: '  -125, -22',
      image: '/images/drilling.jpg'
    },
    {
      title: '',
      description: '    ',
      equipment: ' Caterpillar, Komatsu',
      image: '/images/excavation.jpg'
    },
    {
      title: '',
      description: '   ',
      equipment: ' CAT, JCB',
      image: '/images/loading.jpg'
    },
    {
      title: '',
      description: '    ',
      equipment: '  Sandvik',
      image: '/images/crushing.jpg'
    }
  ];

  const miningServices = [
    {
      title: ' ',
      description: '    ',
      includes: [
        ' ',
        ' ',
        ' ',
        ' '
      ],
      image: '/images/surveying.jpg'
    },
    {
      title: ' ',
      description: '    ',
      includes: [
        ' ',
        ' ',
        '- ',
        '  '
      ],
      image: '/images/boring.jpg'
    },
    {
      title: ' ',
      description: '   ',
      includes: [
        ' ',
        ' ',
        ' ',
        ' '
      ],
      image: '/images/blasting.jpg'
    }
  ];

  return (
    <div className="services-page">
      {/* Hero Section */}
      <section className="hero" style={{padding: '4rem 0'}}>
        <div className="container">
          <h1> COMSPEC</h1>
          <p>        </p>
        </div>
      </section>

      {/* Delivery Services */}
      <section className="section" id="delivery-services">
        <div className="container">
          <h2 className="section-title"></h2>
          <div className="grid grid-2">
            {deliveryServices.map((service, index) => (
              <div key={index} className="card" id={`delivery-service-${index}`}>
                <div style={{
                  height: '200px',
                  backgroundColor: '#f8f9fa',
                  borderRadius: '8px',
                  marginBottom: '1.5rem',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: '#6c757d'
                }}>
                   
                </div>
                
                <h3>{service.title}</h3>
                <p>{service.description}</p>
                
                <h4>:</h4>
                <ul style={{marginBottom: '1.5rem'}}>
                  {service.features.map((feature, idx) => (
                    <li key={idx}>{feature}</li>
                  ))}
                </ul>
                
                <button 
                  className="btn btn-accent"
                  onClick={() => handleConsultationClick('delivery')}
                >
                   
                </button>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Equipment Rental */}
      <section className="section dark" id="equipment-services">
        <div className="container">
          <h2 className="section-title"> </h2>
          <div className="grid grid-4">
            {equipmentServices.map((service, index) => (
              <div key={index} className="card" id={`equipment-service-${index}`}>
                <div style={{
                  height: '150px',
                  backgroundColor: '#f8f9fa',
                  borderRadius: '8px',
                  marginBottom: '1rem',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: '#6c757d'
                }}>
                   
                </div>
                
                <h3>{service.title}</h3>
                <p>{service.description}</p>
                <p><strong>:</strong> {service.equipment}</p>
                
                <button 
                  className="btn btn-accent"
                  onClick={() => handleConsultationClick('equipment')}
                >
                   
                </button>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Mining Services */}
      <section className="section" id="mining-services">
        <div className="container">
          <h2 className="section-title"> </h2>
          <div className="grid grid-3">
            {miningServices.map((service, index) => (
              <div key={index} className="card" id={`mining-service-${index}`}>
                <div style={{
                  height: '180px',
                  backgroundColor: '#f8f9fa',
                  borderRadius: '8px',
                  marginBottom: '1.5rem',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: '#6c757d'
                }}>
                   
                </div>
                
                <h3>{service.title}</h3>
                <p>{service.description}</p>
                
                <h4>:</h4>
                <ul style={{marginBottom: '1.5rem'}}>
                  {service.includes.map((item, idx) => (
                    <li key={idx}>{item}</li>
                  ))}
                </ul>
                
                <button 
                  className="btn btn-accent"
                  onClick={() => handleConsultationClick('mining')}
                >
                   
                </button>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Additional Info */}
      <section className="section gray" id="additional-services">
        <div className="container">
          <div className="grid grid-2">
            <div>
              <h3>   ?</h3>
              <ul>
                <li>    </li>
                <li>  </li>
                <li>  </li>
                <li>  </li>
                <li>   </li>
                <li>   </li>
              </ul>
            </div>
            
            <div>
              <h3> </h3>
              <ul>
                <li>-  </li>
                <li>-    </li>
                <li>-  </li>
                <li>-  </li>
                <li>-  </li>
                <li>-  </li>
              </ul>
            </div>
          </div>
          
          <div style={{textAlign: 'center', marginTop: '2rem'}}>
            <button 
              className="btn btn-primary"
              onClick={() => handleConsultationClick('general')}
            >
               
            </button>
          </div>
        </div>
      </section>

      {/*      */}
      {process.env.NODE_ENV === 'development' && (
        <div style={{ 
          position: 'fixed', 
          bottom: '20px', 
          right: '20px', 
          background: '#008080', 
          color: 'white', 
          padding: '10px', 
          borderRadius: '5px',
          fontSize: '12px',
          zIndex: 1000,
          maxWidth: '200px'
        }}>
          <div><strong> Services Dev Tools</strong></div>
          <button onClick={() => {
            const results = search('', 3);
            console.log(' "":', results);
            highlight('', 'service');
          }} style={{ margin: '2px', padding: '3px', fontSize: '10px' }}>
             ""
          </button>
          <button onClick={() => {
            const results = search('', 3);
            console.log(' "":', results);
            highlight('', 'service');
          }} style={{ margin: '2px', padding: '3px', fontSize: '10px' }}>
             ""
          </button>
          <button onClick={() => {
            const results = search('', 3);
            console.log(' "":', results);
            highlight('', 'service');
          }} style={{ margin: '2px', padding: '3px', fontSize: '10px' }}>
             ""
          </button>
          <button onClick={() => clearHighlights()} style={{ margin: '2px', padding: '3px', fontSize: '10px' }}>
            
          </button>
        </div>
      )}
    </div>
  );
};

export default Services;